<template>
    <div class="row text-center">
        <div class="col" v-if="set.mobile">
            <div class="border rounded"><span class="bi bi-tablet-fill me-1"></span>改手机号</div>
        </div>
        <div class="col" v-if="set.cancel">
            <div class="border rounded"  @click="oncancel"><span class="bi bi-bookmark-x-fill me-1"></span>取消订单</div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
    show:{},
  },
  methods:{
      oncancel(){
         this.$emit('onsubmit',{method: 'post',url:this.set.cancel,params:{token:this.$store.state.token},data:{id:this.show.id,state:7}});
      },
  }
}
</script>

<style>

</style>


