<template>
    <Layout :head_class="'bg-light'" :main_class="'bg-light'" :foot_class="'bg-white'" >
        <template slot="main">
            <div class="position-fixed start-0 end-0 top-0">
                <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="item.data" />
            </div>
            <div class="position-relative">
                <div class="position-absolute start-0 end-0 bottom-0">
                    <div class="shadow rounded bg-info m-2">
                        <div v-for="(item,key) in list" :key="key" class="d-flex justify-content-between align-items-center text-white p-2" @click="$tourl(item.url)">
                            <div class="bd-highlight">
                                <div class="f4">{{item.title}}</div>
                                <div class="fs-normal">
                                    <span>{{item.setdate|setdate(item.setdate_time)}}</span>
                                    <span class="inbefore">{{item.origin}}</span><span class="inbefore">{{item.destination}}</span>
                                </div>
                            </div>
                            <div class="bd-highlight"><span class="bi bi-chevron-right"></span></div>
                        </div>
                        <div class="rounded bg-white p-2">
                            <com-form :data="set.form" :form="form" @getinfo="getinfo"/>
                        </div>
                    </div>
                </div>
                <div class="position-absolute start-0 end-0 top-0">
                      <View-group v-for="(item,key) in group" :key="key" :set="item.set" :type="item.type" :data="data" />
                </div>
            </div>
        </template>
    </Layout>
</template>

<script>
import Layout from './public-amap'
import ViewGroup from '../group/start'
import axios from 'axios'
export default {
  components: {
    Layout,
    ViewGroup,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
        list:[],
        form:{"origin": "","origin_lat": "","origin_lng": "","destination": "","destination_lat": "","destination_lng": ""},
    }
  },
  mounted(){
      this.getlist();  
  },
  methods:{
      getinfo(key){
         if(key.origin && key.destination){
             this.$tourl('passenger_edit',key);
         }
      },
      getlist(key){
        if(this.$store.state.token){
            axios({method: 'post',url: this.$store.state.apiset.url+'/passenger/list',params:{token:this.$store.state.token},data:this.form,}).then(response => {
            let res=response.data;
            if(res.code==1){
                if(res.data){
                this.list=res.data;
                }
            } else if (res.code == 401) {
            }else{
            this.$toast({type:'result',data:res});
            }
            }).catch(function (error) {console.log(error)})
        }
      }
  }
}
</script>
<style scoped>
.inbefore::after{
  content: "-";
  margin:0px 2px;

}
.inbefore+.inbefore::after {
     display:none;
}
</style>


