import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export default new Vuex.Store({
    state: {
        apiset: {
            url: 'http://www.ecgam.com/index.php/api'
        },
        imgurl: 'http://www.ecgam.com/public',
        userinfo: {},
        token: localStorage.getItem('token') || '',
        origin: localStorage.getItem('origin') || '',
        origin_lng: localStorage.getItem('origin_lng') || '',
        origin_lat: localStorage.getItem('origin_lat') || '',
    },
    getters: {

    },
    mutations: {
        auth_request(state) {
            state.status = 'loading';
        },
        auth_success(state, token, user) {
            state.status = 'success';
            state.token = token;
            state.userinfo = user;
        },
        auth_error(state) {
            state.status = 'error';
        },
        logout(state) {
            state.status = '';
            state.token = '';
        },
        set_origin(state, origin, origin_lng, origin_lat) {
            state.origin = origin;
            state.origin_lng = origin_lng;
            state.origin_lat = origin_lat;
        },
    }
})


