<template>
    <div class="bg-light">
        <div class="position-fixed top-0 start-0 end-0 bottom-0  bg-light">
            <div id="container" class="h-100"></div>
        </div>
        <div class="position-fixed top-0 start-0">
        <span @click="$tourl('../')"  class="fs-4 bi bi-chevron-left m-2"></span>
        </div>
        <div class="bg-white position-fixed start-0 end-0 bottom-0 rounded-top-lg px-3 pb-1">
            <div class="text-center" @click="isshow=isshow==='data'?'':'data'"><span class="bi fs-2 text-black-50" :class="isshow==='data'?'bi-chevron-compact-up':'bi-chevron-compact-down'"></span></div>
            <div  v-for="(item,key) in setdata[set.binding].stateinfo[data.show.state]" :key="key" class="pb-1 fs-normal">
                <View-group v-if="key!==isshow" :set="item" :type="key" :show="data.show" :data="data"  @onsubmit="onsubmit"/>
            </div>
        </div>
    </div>
</template>

<script>
import setdata from './trip/setdata.json';
import ViewGroup from './trip/start'
export default {
  components: {
    ViewGroup,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      setdata,
      isshow:'',
    }
  },
  mounted(){
    window.ysdt._this=this;
    if(this.data.show.origin && this.data.show.destination){
      window.ysdt.showdriving([this.data.show.origin_lng,this.data.show.origin_lat],[this.data.show.destination_lng,this.data.show.destination_lat]);
    }
  },
  methods:{
      onsubmit(key){
         this.$emit('onsubmit',key);
      },
  }
}
</script>



