<template>
  <div>
      <div class="bg-white d-flex justify-content-between align-items-center p-1" v-if="data.user">
        <div class="bd-highlight">
          <span @click="$tourl('../')" class="fs-5 bi bi-chevron-left"></span>
        </div>
        <div class="bd-highlight">
            <span class="fs-6">{{data.user.nickname}}</span>
        </div>
        <div class="bd-highlight">
        </div>
      </div>
      <div class="bg-white d-flex justify-content-between align-items-center p-1" v-if="data.show">
        <div class="bd-highlight">
          <span @click="$tourl('../')" class="fs-5 bi bi-chevron-left"></span>
        </div>
        <div class="bd-highlight">
            <span class="fs-6">{{data.show.nickname}}</span>
        </div>
        <div class="bd-highlight">
        </div>
      </div>
      <div class="bg-white d-flex justify-content-between align-items-center" v-if="data.show">
        <div class="bd-highlight p-2">
          <div class="text-black">明天11:20-11:55 | 待接单</div>
          <div class="text-black">
            <span class="inbefore">{{data.show.origin}}</span>
            <span class="inbefore">{{data.show.destination}}</span>
          </div>
        </div>
        <div class="bd-highlight p-2">
          <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-black-50 mx-1 bi bi-telephone-fill" style="width: 30px; height: 30px;"></span>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  methods:{
      
  }
}
</script>
<style scoped>
.inbefore::before {
    width: .25rem;
    height: .25rem;
    display: inline-block;
    margin-right: 0.2rem;
    background-color: #0d6efd;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    content: " ";
}
.inbefore+.inbefore::before {
    background-color: #198754;
}
.inbefore::after{
  content: "-";
  color: rgba(0, 0, 0, .5)!important;
  margin:0px 2px;

}
.inbefore+.inbefore::after {
     display:none;
}
</style>


