<template>
    <div class="d-grid">
        <button v-if="set.state" class=" btn btn-primary" @click="onsubmit">{{set.name}}</button>
        <button v-else class=" btn btn-dark" disabled>{{set.name}}</button>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
    show:{},
  },
  methods:{
      onsubmit(){
         this.$emit('onsubmit',{method: 'post',url:this.set.url,params:{token:this.$store.state.token},data:{id:this.show.id,state:this.set.state}});
      },
  }
}
</script>

<style>

</style>


