<template>
  <component :is="'trip-'+type" :set="set" :data="data" :show="show" @onsubmit="onsubmit"></component>
</template>

<script>
import tripInfo from './trip-info'
import tripPay from './trip-pay'
import tripUser from './trip-user'
import tripData from './trip-data'
import tripNav from './trip-nav'
import tripSubmit from './trip-submit'
export default {
  components: {
    tripInfo,
    tripPay,
    tripUser,
    tripData,
    tripNav,
    tripSubmit,
  },
  props: {
    type:'',
    set:{},
    data:{},
    show:{},
  },
  methods:{
      onsubmit(key){
         this.$emit('onsubmit',key);
      },
  }
}
</script>


