<template>
<div class="bg-white">
  <div class="d-flex justify-content-between align-items-center p-1">
    <div class="bd-highlight">
      <span @click="$tourl('../')" class="fs-5 bi bi-chevron-left"></span>
    </div>
    <div class="bd-highlight">
        <span class="fs-6 me-2">{{set.title}}</span>
    </div>
    <div class="bd-highlight">
        <span class="text-black-50 fs-normal" @click="onselect">取消订单</span>
    </div>
  </div>
  <div class="d-flex justify-content-between p-2">
    <div class="bd-highlight fs-normal">
      <div><span class="inbefore">{{show.origin}}</span><span class="inbefore">{{show.destination}}</span></div>
      <div class="text-black-50 fs-normal">{{show.setdate|setdate}} | 可载{{show.people}}人</div>
    </div>
    <div class="bd-highlight">
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  data(){
      return {
          show:{},
          loading:false,
      }
  },
  created(){
      this.show=this.data.search;
  },
  methods:{
    onselect(){
      this.$toast({type:'select',data:[{name:'重新发布行程',url:'/driver/cancel',id:this.show.id},{name:'取消当前行程',url:'/driver/cancel',id:this.show.id}]}).then((e) => {
        this.onaxios({method: 'post',url: e.url,params:{token:this.$store.state.token},data:{id:e.id}});
      });
    },
    onaxios(key){
        this.loading=true;
        key.url=this.$store.state.apiset.url+key.url;
        axios(key).then(response => {
            this.loading=false;
            let res=response.data;
            if(res.code==1){

             }else{
               this.$toast({type:'result',data:res});
             }
          }).catch(function (error) {this.loading=false;console.log(error)})
    }
  }
}
</script>
<style scoped>
.inbefore::before {
    width: .25rem;
    height: .25rem;
    display: inline-block;
    margin-right: 0.2rem;
    background-color: #0d6efd;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    content: " ";
}
.inbefore+.inbefore::before {
    background-color: #198754;
}
.inbefore::after{
  content: "-";
  color: rgba(0, 0, 0, .5)!important;
  margin:0px 2px;

}
.inbefore+.inbefore::after {
     display:none;
}
</style>


