    // 日期格式化
Date.prototype.format = function(format) {　　
    var args = {　　　　
        "M+": this.getMonth() + 1,
        "d+": this.getDate(),
        "h+": this.getHours(),
        "m+": this.getMinutes(),
        "s+": this.getSeconds(),
        "q+": Math.floor((this.getMonth() + 3) / 3), //quarter
        "S": this.getMilliseconds()　　
    };　　
    if (/(y+)/.test(format)) format = format.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));　　
    for (var i in args) {　　　　 var n = args[i];　　　　 if (new RegExp("(" + i + ")").test(format)) format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? n : ("00" + n).substr(("" + n).length));　　 }　　
    return format;
};
Vue.filter('setdate', function(sdate, edate = '', val = '') {
    if (!sdate) return val;
    sdate = new Date(sdate).format("yyyy-MM-dd h:mm");
    let sname = sdate.replace(new Date(new Date().getTime() + 0 * 60 * 60 * 1000).format("yyyy-MM-dd"), '今天');
    sname = sname.replace(new Date(new Date().getTime() + 24 * 60 * 60 * 1000).format("yyyy-MM-dd"), '明天');
    sname = sname.replace(new Date(new Date().getTime() + 48 * 60 * 60 * 1000).format("yyyy-MM-dd"), '后天');
    sname = sname.replace(new Date(sdate).format("yyyy-MM-dd"), new Date(sdate).format("M月d日"));
    if (edate) {
        edate = new Date(edate).format("yyyy-MM-dd h:mm");
        let ename = edate.replace(new Date(new Date().getTime() + 0 * 60 * 60 * 1000).format("yyyy-MM-dd"), '今天');
        ename = ename.replace(new Date(new Date().getTime() + 24 * 60 * 60 * 1000).format("yyyy-MM-dd"), '明天');
        ename = ename.replace(new Date(new Date().getTime() + 48 * 60 * 60 * 1000).format("yyyy-MM-dd"), '后天');
        ename = ename.replace(new Date(edate).format("yyyy-MM-dd"), new Date(edate).format("M月d日"));
        ename = ename.replace((sname.split(' ')[0]) + ' ', '');
        return sname + '~' + ename;
    }
    return sname;
})
Vue.filter('timeint', function(msg) {
    if (!msg) return "";
    return Math.floor((new Date().getTime() - msg * 1000) / (24 * 3600 * 1000));
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    store,
    router,
    components: { App },
    template: '<App/>'
})


