<template>
  <div class="bg-white rounded p-2 position-relative fs-normal text-dark ">
    <div class="d-flex justify-content-between border-bottom border-light py-1">
      <div class="bd-highlight">
        <span><span class="text-primary fw-bold fs-6">70%</span>顺路度</span>
        <span>今天 13:00前</span>
      </div>
      <div class="bd-highlight">
        <span class="fw-bold fs-6">17.5</span>元
