      <div class="d-inline-block me-1 border border-black-50 text-black-50 rounded rounded-20 p-1 px-2 fs-sm">素质较高<span class="ms-1">1</span></div>
      <div class="d-inline-block me-1 border border-black-50 text-black-50 rounded rounded-20 p-1 px-2 fs-sm">素质较高<span class="ms-1">1</span></div>
      <div class="d-inline-block me-1 border border-black-50 text-black-50 rounded rounded-20 p-1 px-2 fs-sm">素质较高<span class="ms-1">1</span></div>
    </div>
  </div>
</template>

<script>
import Userlogo from "../icons/user-logo";
export default {
  components: {
    Userlogo,
  },
  props: {
    set:{},
    data:{},
  },
}
</script>



