<template>
 <com-layout :layout_class="set.layout_class" :head_class="set.head_class" :main_class="set.main_class" :foot_class="set.foot_class">
    <template slot="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="data" />
    </template>
    <template slot="main">
    <div style="background-color: #0dcaf0; background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0), rgb(248, 249, 250) 230px);">
        <div class="py-1 px-2">
            <div class="p-5 text-center text-white">
                <div>
                    <div>可用金额(元)</div>
                    <div class="fs-1 fw-bold">{{show.money}}</div>
                </div>
            </div>
            <div class="bg-white p-4 rounded shadow-sm fs-normal">
                <div class="d-flex justify-content-around">
                    <div class="bd-highlight text-center">
                        <div class="text-black-50">我的金额(元)</div>
                        <div class="fs-4 fs-bold">{{show.money}}</div>
                    </div>
                    <div class="bd-highlight text-center" @click="$tourl('user_trip-driver')">
                        <div class="text-black-50">其它费用(元)</div>
                        <div class="fs-4 fs-bold">-</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </template>
    <template slot="foot">
      <View-group v-for="(item,key) in set.foot" :key="key" :set="item" :type="item.type" :data="item.data" />
    </template>
  </com-layout>
</template>


<script>
import ViewList from '../block/list';
import LayoutFrame from '../block/layout/frame-1';
import ViewGroup from '../group/start'
export default {
  components: {
    ViewGroup,
    LayoutFrame,
    ViewList,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      show:{},
    }
  },
  created(){
    this.show=this.data.userinfo;
  },
}
</script>

<style>

</style>


