<template>
 <div class="bg-light">
    <div ref="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="data" />
    </div>
    <div ref="main" class="overflow-auto" :style="{height:bodyheight+'px'}">
        <div>
        <div class="p-2 fs-normal">
            <div  v-for="(item,index) in infolist" :key="index">
                <div class="d-flex flex-row-reverse align-items-end  pt-2" v-if="item.user_id===data.userinfo.id">
                    <div class="bd-highlight">
                        <img :src="$store.state.imgurl+'/assets/img/userimg/'+item.avatar+'.jpg'" width="40" height="40" class="rounded-pill border border-2 border-white">
                    </div>
                    <div class="bd-highlight">
                        <Viewmy :show="item" />
                    </div>
                    <div class="bd-highlight">
                        <span v-if="item.state==='2'">未读</span>
                        <span v-if="item.state==='1'">已读</span>
                        <span v-if="item.state==='0'">发送失败</span>
                    </div>
                </div>
                <div class="d-flex flex-row align-items-end pt-2" v-else>
                    <div class="bd-highlight">
                        <img :src="$store.state.imgurl+'/assets/img/userimg/'+item.avatar+'.jpg'" width="40" height="40" class="rounded-pill border border-2 border-white">
                    </div>
                    <div class="bd-highlight">
                        <Viewta :show="item" />
                    </div>
                </div>
            </div>
        </div>
        </div>
    </div>
    <div ref="foot" class="bg-white p-2">
        <div class="row g-0">
            <div class="col-auto">
                <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark me-2 bi bi-mic-mute-fill" style="width:25px; height: 25px;"></span>
            </div>
            <div class="col">
                <div class="d-grid">
                    <input ref="input" class="form-control form-control-sm bg-light border-0" v-model="text" @keyup="onvalue" placeholder="请输入内容" @click="onisinfo(false)"/>
                </div>
            </div>
            <div class="col-auto">
                <div v-if="text" class="px-2">
                    <button class=" btn-sm btn-success" @click="onsubmit({text:text})" :disabled="loading">发送{{loading?'..':''}}</button>
                </div>
                <div v-else>
                    <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark ms-2 bi" :class="isInfo===true?'bi-grid-3x2-gap':'bi-chat-dots'" @click="onisinfo()" style="width: 25px; height: 25px;"></span>
                    <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark ms-2 bi bi-geo-alt" style="width: 25px; height: 25px;"></span>
                </div>
            </div>
        </div>
    </div>
    <div v-if="isInfo" class="overflow-auto" :style="{height:submitheight+'px'}">
        <div  v-for="(item2,index2) in set.chat_data" :key="index2" class="bg-white border-bottom border-light p-2" @click="onsubmit({text:item2})">{{item2}}</div>
    </div>
</div>
</template>


<script>
import ViewGroup from '../group/start'
import Viewmy from './chat/my';
import Viewta from './chat/ta';
import axios from 'axios'
export default {
  components: {
    ViewGroup,
      Viewmy,
      Viewta,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
      return {
        isInfo:true,
        loading:false,
        submit_int:0,
        text:"",
        infolist:[],
        bodyheight:0,
        headheight:0,
        footheight:0,
        submitheight:41*5,
        chatparams:{},
      }
  },
   mounted(){
    this.headheight=this.$refs.head.offsetHeight;
    this.footheight=this.$refs.foot.offsetHeight;
    this.$refs.main.scrollTop=this.$refs.main.scrollHeight;
    this.getisinfo();
    const _this = this;
    // 监听浏览器窗口变化
    window.onresize = function() {
      return (() => {
        _this.bodyheight = window.innerHeight-_this.headheight-_this.footheight;
      })();
    };
    if(this.data.show){
        this.chatparams={token:this.$store.state.token,type:this.set.chat_type,tid:this.data.show.id,nid:this.data.show.user_id};
    }else if(this.data.user){
        this.chatparams={token:this.$store.state.token,nid:this.data.user.user_id};
    }
    this.onrefresh(this.$route.path.split('/').pop(),this.submit_int);
  },
  methods:{
    onvalue(){
    },
    onisinfo(key=''){
        if(key===''){
            this.isInfo=this.isInfo?false:true;
        }else{
            this.isInfo=key;
        }
        this.getisinfo();
    }, 
    getisinfo(){
        if(this.isInfo){
            this.bodyheight=window.innerHeight-this.headheight-this.footheight-this.submitheight;
        }else{
            this.$refs.input.focus();
            this.bodyheight=window.innerHeight-this.headheight-this.footheight;
        }
        this.setck();
    },
    setck(){
        setTimeout(() => {
        this.$refs.main.scrollTop=this.$refs.main.scrollHeight;
        }, 10)
    },
    onsubmit(data){
        if(this.loading===false){
            this.loading=true;
            this.submit_int++;
            axios({method: 'post',url: this.$store.state.apiset.url+'/chat/sendout',params:this.chatparams,data:data}).then(response => {
                this.loading=false;
                let res = response.data;
                if(this.infolist.length!==res.data.length){
                    this.infolist = res.data;
                    this.getisinfo();
                    this.onrefresh(this.$route.path.split('/').pop(),this.submit_int);
                }
            }).catch(function (error) {this.loading=false;console.log(error)});
            this.text='';
            this.$refs.input.focus();
        }
    },
    onrefresh(viewkey,submit_int) {
        axios({method: 'post',url: this.$store.state.apiset.url+'/chat/getdata',params:this.chatparams,data:{}}).then(response => {
            let res = response.data;
            if (res.data) {
                setTimeout(() => {
                    if (viewkey===this.$route.path.split('/').pop() && submit_int===this.submit_int) {
                        if(this.infolist.length!==res.data.length){
                            this.infolist = res.data;
                            this.getisinfo();
                        }
                        this.onrefresh(viewkey,submit_int);
                    }
                }, 300)
            }
        }).catch(function (error) {console.log(error)})
    }
  }
}
</script>

<style>

</style>


