<template>
  <div class="bg-white rounded p-2 fs-normal position-relative fs-normal" @click="$emit('geturl','id',data.id)">
      <div class="d-flex justify-content-between align-items-center">
          <div class="bd-highlight">
              <div class="p-1">
                <!-- <span class="badge bg-info">顺风车</span> -->
                <span class="text-primary fs-6">{{data.title}}</span>
                <span>{{data.setdate|setdate(data.setdate_time)}}</span>
              </div>
              <div class="p-1">
                <span class="inbefore">{{data.origin}}</span>
                <span class="inbefore">{{data.destination}}</span>
              </div>
          </div>
          <div class="bd-highlight">
              <span class="bi bi-chevron-right"></span>
          </div>
      </div>
  </div>
</template>

<script>
import Userlogo from "../icons/user-logo";
export default {
  components: {
    Userlogo,
  },
  props: {
    set:{},
    data:{},
  },
}
</script>
<style scoped>
.inbefore::before {
    width: .25rem;
    height: .25rem;
    display: inline-block;
    margin-right: 0.2rem;
    background-color: #0d6efd;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    content: " ";
}
.inbefore+.inbefore::before {
    background-color: #198754;
}
.inbefore::after{
  content: "-";
  color: rgba(0, 0, 0, .5)!important;
  margin:0px 2px;

}
.inbefore+.inbefore::after {
     display:none;
}
</style>


