<template>
<div>
   <Layout v-if="data.set" :type="data.type" :set="data.set" :data="data.data" :group="data.group" @refresh="ispathaxios"/>
   <div v-if="loading" class="position-fixed top-0 start-0 end-0 bottom-0 ">
     <div class="position-absolute top-50 start-50 translate-middle"><span class="spinner-border"></span></div>
   </div>
</div>
</template>

<script>
import pathmethods from '@/router/axios-ajax'
import Layout from '../components/layout/start'
export default {
  components: {
    Layout,
  },
  props: {
    datacache:{},
  },
  data() {
    return {
      data:{},
      viewkey:'index',
      tips:[],
      loading:false,
    };
  },
  created () {
    this.ispathMatch();
  },
  watch:{
     $route(){
        this.ispathMatch();
     }
  },
  methods:pathmethods
};
</script>


