<template>
    <Common :datacache="datacache"/>
</template>

<script>
import driverEdit from '@/json/driver-edit.json';

import loginJson from '@/json/login.json';
import Common from "./common";

export default {
  // 注册 channel
  components: {
    Common,  
  },
  data() {
    return {
      datacache:{
        login:loginJson,
        "driver_edit":driverEdit,
        },
    };
  },
   
};

</script>


