<template>
    <div class="container-fluid">
        <div :class="set.layout_class">
            <Inner v-for="(item,index) in data" :key="index" :data="item" :type="set.inner_type" :set="set" @click="getinfo(item)"></Inner>
        </div>
        <Empty v-if="set.empty&&data.length===0" :data="set.empty" @getinfo="getinfo"></Empty>
    </div>
</template>

<script>
import Inner from './inner/start.js'
import Empty from './empty/start'
export default {
    components:{
        Inner,
        Empty,
    },
    props: {
        set:{},
        data:{}
    },
    methods:{
        getinfo(key){
            this.$emit('getinfo',key);
        },
    }
}
</script>



