<template>
  <component :is="type?type:'login'" :set="set" :group="group" :data="data" @getinfo="getinfo" @onsubmit="onsubmit"/>
</template>

<script>
import login from "./login";
import show from "./show";
import chat from "./chat";
import myuser from "./myuser";
import mymoney from "./mymoney";

import user from "./user";
import showScroll from "./show-scroll";
import amapShow from "./amap-show";
import passengerIndex from "./passenger-index";
import passengerEdit from "./passenger-edit";
import edit from "./edit";
import axios from 'axios'
export default {
  components: { 
      login,
      show,
      chat,
      myuser,
      mymoney,
      user,
      edit,
      amapShow,
      passengerIndex,
      passengerEdit,
      showScroll,
  },
  props: {
    type:'',
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      loading:false,
    }
  },
  methods:{
    getinfo(key){
        this.$emit('getinfo',key);
    },
    onsubmit(key){
        this.loading=true;
        key.url=this.$store.state.apiset.url+key.url;
        axios(key).then(response => {
            this.loading=false;
            let res=response.data;
            if(res.code==1){
              if(res.data){
                if(res.data.userinfo){
                  this.handle_userinfo(res);
                }
                if(res.data.go){
                  this.$tourl('./');
                }
                console.log(res);
              }else{
                console.log(res);
             // this.$router.push('/passenger');
              }
            }else if(res.code==5){
              this.$toast({type:'result',data:res});
              this.$emit('refresh');
            }else if(res.code==6){
               this.$tourl('./');
            }else{
               this.$toast({type:'result',data:res});
            }
          }).catch(function (error) {this.loading=false;console.log(error)})
    },
    handle_userinfo(res){
      const token = res.data.userinfo.token;
      const user = res.data.userinfo;
      localStorage.setItem('token', token)
      // 每次请求接口时，需要在headers添加对应的Token验证
      axios.defaults.headers.common['Authorization'] = token
      // 更新token
      this.$store.commit('auth_success', token, user);
      let pathurl=this.$route.path;
      pathurl=pathurl.replace(RegExp("/login", "g"),'');
      this.$router.push(pathurl);
    }
  }
}
</script>


