<template>
  <div class="d-flex justify-content-between p-2">
    <div class="bd-highlight">
      <div>车主通常需要<span class="text-primary">10-15分钟</span>接单，请耐心等待</div>
      <div class="text-black-50 fs-normal"><span class="ybeforetwo">客路村</span><span class="ybeforetwo">广东海洋大学</span></div>
      <div v-show="isActive" class="text-black-50 fs-normal">
        <span class="ybefore">{{data.search.setdate|setdate(data.search.setdate_time)}}</span>
        <span class="ybefore">{{data.search.people}}人</span>
      </div>
    </div>
    <div>
      <span class="bi " :class="isActive===true?'bi-chevron-up':'bi-chevron-down'" @click="isActive=isActive===true?false:true"></span>
    </div>
  </div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  data(){
    return {
      isActive:false,
    }
  }
}
</script>


