<template>
    <div>
        <div class="input-group">
            <span class="input-group-text">+86</span>
            <input type="text" class="form-control" :class="{'border-danger':data.validate}" :placeholder="data.placeholder" v-model="form[name]" @keyup="onvalue"/>
        </div>
        <div class="input-group pt-2">
            <span class="input-group-text">
                <span class="bi bi-pencil-square"></span>
            </span>
            <input type="text" class="form-control" v-model="form['captcha']" />
            <button class="btn-warning" type="button" @click="onsubmit" :disabled="loading">{{submitname}}{{submit_number>0?'('+submit_number+')':''}}</button>
        </div>
    </div>  
</template>

<script>
import axios from 'axios'
export default {
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
        loading:false,
        submitname:'发送验证码',
        submit_number:0,
        timer: '',
      }
  },
  methods:{
    onvalue(){
      if(typeof this.data.validate!=='undefined'){
          this.data.validate=false
      }
    },   
    getnumber(){
        if(this.submit_number <= 0){
            this.loading=false;
            clearInterval(this.timer);
        }else{
            this.submit_number--
        }
    },  
    onsubmit(){
        if(this.loading===false){
            this.loading=true;
            axios({method: 'post',url: this.$store.state.apiset.url+this.data.accept,data:{mobile:this.form[this.name]}}).then(response => {
                let res = response.data;
                if (res.code===1) {
                    this.submit_number=60;
                    this.submitname = '重新发送';
                    this.timer = setInterval(this.getnumber,1000);
                    this.$toast({type:'result',data:res});
                }else{
                   this.loading=false;
                   this.$toast({type:'result',data:res});
                }
            }).catch(function (error) {this.loading=false;console.log(error)});
            this.text='';
        }
    },
  }
}
</script>

<style>

</style>


