<template>
    <Common :datacache="datacache"/>
</template>

<script>
import indexJson from '@/json/passenger.json';
import passengerEdit from '@/json/passenger-edit.json';

import loginJson from '@/json/login.json';

import Common from "./common";
export default {
  // 注册 channel
  components: {
    Common,  
  },
  data() {
    return {
      datacache:{
        index:indexJson,
        passenger:indexJson,
        "passenger_edit":passengerEdit,
        login:loginJson,
        },
    };
  },
   
};

</script>


