<template>
    <div class="d-flex justify-content-between align-items-end">
        <div class="bd-highlight">
            <div>
                <span class="ybefore">{{show.setdate|setdate(show.setdate_time)}}</span>
                <span class="ybefore">{{show.people}}人</span>
                <span class="ybefore">{{show.carpooling==='1'?'愿拼车':'不拼车'}}</span>
            </div>
            <div class="ymerge">{{show.origin}}</div>
            <div class="ymerge">{{show.destination}}</div>
        </div>
        <div>
            <div>
                <span class="fs-4">{{show.price}}</span>元<span class="bi bi-chevron-right"></span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  props: {
    set:{},
    data:{},
    show:{},
  },
}
</script>

<style>

</style>


