<template>
    <div class="d-flex justify-content-between align-items-center py-2">
        <div class="d-flex flex-row bd-highlight">
            <div class="bd-highlight">
                <img :src="$store.state.imgurl+'/assets/img/userimg/'+show.avatar+'.jpg'" width="50" height="50" class="rounded-pill border border-2 border-white">
            </div>
            <div class="bd-highlight ms-2">
                <div class="fw-bold fs-6">{{show.nickname}}</div>
                <div>{{show.pevaluate}} 分 - 出行<span class="text-primary">{{show.pint}}</span>次</div>
            </div>
        </div>
        <div class="bd-highlight">
            <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-black-50 mx-1 bi bi-telephone-fill"  style="width:30px; height:30px;"></span>
            <span class="d-inline-block text-center border rounded-pill bg-white fs-6 text-dark mx-1 bi bi-chat-dots-fill"  style="width:30px; height:30px;" @click="$tourl('tchat-'+show.id)"></span>
        </div>
    </div>
</template>

<script>
import Iconspill from "../../block/icons/pill-icons";
export default {
  components: {
    Iconspill,
  },
  props: {
    set:{},
    data:{},
    show:{},
  }
}
</script>



