<template>
    <div :class="set.layout_class">
      <com-form :data="set.form.data" :form="form"/>
      <div :class="set.submit_class">
          <div v-if="set.form.accept" class="d-grid pt-2">
                <button class=" btn btn-primary" @click="onsubmit">确定</button>
          </div>
      </div>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      form:{},
    }
  },
  created(){
    for(let x in this.set.form.names){
     this.$set(this.form,this.set.form.names[x],this.data[this.set.form.type][this.set.form.names[x]]);
    }
  },
  methods:{
      onvalidate(){
          for(let x in this.form){
            if(this.form[x]==="" || this.form[x].length===0){
              if(this.set.form[x]){
                if(this.set.form[x].validate){
                this.set.form[x].isActive=true
                return 0;
                }
              }
            }
          }
          return 1;
      },
      onsubmit(){
        if(this.onvalidate()===1){
         this.$emit('onsubmit',{method: 'post',url:this.set.form.accept,params:{token:this.$store.state.token,result:{go:1}},data:this.form,});
        }
      }
  }
}
</script>

<style>

</style>


