import axios from 'axios'
export default {
    ispathMatch() {
        let patharr = this.$route.path.split('/');
        for (let i = 0; i < patharr.length; i++) {
            if (patharr[i] == "" || typeof(patharr[i]) == "undefined") {
                patharr.splice(i, 1);
                i = i - 1;
            }
        }
        this.viewkey = patharr.pop();
        this.ispathaxios();
    },
    ispathaxios() {
        if (this.datacache[this.viewkey]) {
            this.data = '';
            this.data = this.datacache[this.viewkey];
            this.$forceUpdate();
        } else {
            this.pathaxios(this.viewkey);
        }
    },
    pathaxios(viewkey) {
        if (this.loading == false) {
            this.loading = true;
            axios.get(this.$store.state.apiset.url + '/index/layout/class/' + viewkey + '?token=' + this.$store.state.token)
                .then(response => {
                    this.loading = false;
                    let res = response.data;
                    if (res.code == 1) {
                        if (res.data) {
                            // this.datacache[viewkey] = res.data;
                            this.data = res.data;
                            if (this.data.switch === 1) {
                                this.onrefresh(viewkey);
                            }
                        } else {
                            this.$router.push('/passenger');
                        }
                        // console.log(res.msg);
                    } else if (res.code == 401) {
                        this.$tourl('login');
                        console.log(res.msg);
                    } else if (res.code == 403) {
                        this.$tourl('login');
                        console.log(res.msg);
                    } else {
                        console.log(res.msg);
                    }
                }).catch(function(error) {
                    this.loading = false;
                    console.log(error)
                })
        }
    },
    onrefresh(viewkey) {
        axios.get(this.$store.state.apiset.url + '/index/layout/class/' + viewkey + '?token=' + this.$store.state.token)
            .then(response => {
                let res = response.data;
                if (res.data) {
                    setTimeout(() => {
                        if (this.data.switch === 1) {
                            if (this.loading === false && viewkey === this.viewkey) {
                                this.data = res.data;
                                this.onrefresh(viewkey);
                            }
                        }
                    }, 300)
                }
            }).catch(function(error) {
                console.log(error)
            })
    }
}


