<template>
    <div class="bg-info text-white d-flex justify-content-between rounded-bottom p-2 fs-6">
      <div class="bd-highlight">
        <span  v-for="(item,index) in list" :key="index" class="mx-1" :class="ispath===item.url?'border-bottom border-white':''" @click="ispath===item.url?'':$tourl('/'+item.url)">{{item.name}}</span> 
      </div>
      <div class="bd-highlight">
        <span class="fs-5 bi bi-person-lines-fill" @click="$tourl('myuser')"></span>
      </div>
    </div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  },
  data(){
    return {
      list:[{name:'乘客',url:'passenger'},{name:'司机',url:'driver'}],
      ispath:""
    }
  },
  created(){
     let patharr = this.$route.path.split('/');
     this.ispath=patharr[1];
  },
  methods:{
      
  }
}
</script>

<style>

</style>


