<template>
    <div>
      <div class="fs-6 text-center" v-if="show.ispay===1 && set.pay">{{set.pay}}</div>
      <div class="fs-6 text-center" v-if="show.ispay===0 && set.nopay">{{set.nopay}}</div>
      <div class="fs-6 text-center" v-if="set.title">{{set.title}}</div>
      <div class="text-black-50 fs-sm text-center" v-if="set.text">{{set.text}}</div>
      <div class="fs-6 text-center" v-if="set.ltitle">{{set.ltitle}}</div>
      <div class="text-black-50 fs-sm text-center" v-if="set.ltext">{{set.ltext}}</div>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    data:{},
    show:{},
  },
}
</script>

<style>

</style>


