<template>
 <com-layout :layout_class="set.layout_class" :head_class="set.head_class" :main_class="set.main_class" :foot_class="set.foot_class">
    <template slot="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="data" />
    </template>
    <template slot="main">
    <div style="background-color: #0dcaf0; background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0), rgb(248, 249, 250) 200px);">
        <div class="py-1 px-2">
            <div class="d-flex flex-row-reverse  text-white">
                <div class="bd-highlight">
                    <span class="fs-5 bi bi-gear-fill" @click="$tourl('user_set')"></span>
                </div>
            </div>
            <div class="d-flex justify-content-between align-items-center text-white py-2">
                <div class="bd-highlight">
                    <div class="d-flex flex-row bd-highlight">
                        <div class="bd-highlight me-2" @click="$tourl('user_profile')">
                            <img class="rounded-pill border border-2 border-white" :src="$store.state.imgurl+'/assets/img/userimg/'+show.avatar+'.jpg'" width="50" height="50"/>
                        </div>
                        <div class="bd-highlight">
                            <div class="fw-bold fs-6"  @click="$tourl('user_nickname')">{{show.nickname}}</div>
                            <div @click="$tourl('user_mobile')">{{show.mobile}} <span class="bi bi-pencil-square"></span></div>
                        </div>
                    </div>
                </div>
                <div class="bd-highlight"></div>
            </div>
            <div class="bg-white p-3 rounded shadow-sm fs-normal">
                <div class="d-flex justify-content-around">
                    <div class="bd-highlight text-center">
                        <div><span class="fs-4 fs-bold">{{show.createtime|timeint}}</span>天</div>
                        <div class="text-black-50">加入顺风车</div>
                    </div>
                    <div class="bd-highlight text-center" @click="$tourl('mypassenger')" v-if="show.dint>0">
                        <div><span class="fs-4 fs-bold">{{show.dint}}</span>次<span class="bi bi-chevron-right"></span></div>
                        <div class="text-black-50">接单次数</div>
                    </div>
                    <div class="bd-highlight text-center" @click="$tourl('mydriver')">
                        <div><span class="fs-4 fs-bold">{{show.pint}}</span>次<span class="bi bi-chevron-right"></span></div>
                        <div class="text-black-50">出行次数</div>
                    </div>
                </div>
                <div class="d-flex justify-content-around py-2">
                    <div class="bg-light rounded-pill text-center d-inline p-1 px-2 text-black-50 fs-normal" @click="$tourl('mymoney')">
                        <span class="bi bi-megaphone"></span>
                        金额<span class="text-primary fw-bold">{{show.money}}</span>元<span class="bi bi-chevron-right"></span>
                    </div>
                </div>
            </div>
        </div>
        <Layout-frame :icons="'person-fill bg-primary'"  :title="'来自乘客的评价'" v-if="data.plabel">
            <div class="px-1 fs-normal text-black-50">评分 <span class="text-primary">{{show.pevaluate}}</span> 分 {{data.plabel?'':'暂未收到评价标签'}}</div>
            <ViewList :layout="'py-2'" :type="'evaluate'" :set="{names:['','车况较差','态度较差','道路不熟','临时加价','迟到到达','危险驾驶','车内干净','文明守礼','路线熟悉','不多收费用','按约定等候','驾驶技术优秀']}" :data="data.plabel"/>
        </Layout-frame>
        <Layout-frame :icons="'heart-fill bg-warning'" :title="'来自车主的评价'">
            <div class="px-1 fs-normal text-black-50">评分 <span class="text-warning">{{show.devaluate.toFixed(1)}}</span> 分 {{data.dlabel?'':'暂未收到评价标签'}}</div>
            <ViewList :layout="'py-2'" :type="'evaluate'" :set="{names:['','有不合理要求','态度较差','沟通不畅','有不文明行为','未按约定上车','聒噪吵闹','要求合理','文明守礼','沟通顺畅','素质较高','准时上车','安静乘车']}" :data="data.dlabel"/>
        </Layout-frame>
        <Layout-frame :icons="'github bg-warning'" :title="'我的爱车'" v-if="data.user_vehicle">
            <div class="py-2">
                <div  v-for="(item,index) in data.user_vehicle" :key="index" class="d-flex flex-row bd-highlight bg-light p-2 rounded border border-1 border-black-50">
                    <div class="bd-highlight">
                        <img :src="$store.state.imgurl+item.imgurl" width="160" height="80" class="rounded border border-2 border-white">
                    </div>
                    <div class="bd-highlight ps-1">
                        <div class="fs-6 fw-bold">{{item.brand}}</div>
                        <div class="text-black-50 py-1">{{item.typeinfo}} {{item.colour}}</div>
                        <!-- <div>
                            <div class="d-inline-block px-2 border rounded-pill">编辑</div>
                        </div> -->
                    </div>
                </div>
                <!-- <div class="p-2 text-center bg-light mt-2">
                    <span class="bi bi-plus-square-dotted me-2 fs-6"></span>添加车辆
                </div> -->
            </div>
        </Layout-frame>
        <Layout-frame :icons="'lightbulb-fill bg-primary'" :title="'帮助'">
            <div class="p-2">
                <div class="d-flex justify-content-between" @click="$tourl('consult')">
                    <div>客服中心</div>
                    <div class="text-black-50">咨询<span class="bi bi-chevron-right"></span></div>
                </div>
            </div>
        </Layout-frame>
    </div>
    </template>
    <template slot="foot">
      <View-group v-for="(item,key) in set.foot" :key="key" :set="item" :type="item.type" :data="item.data" />
    </template>
  </com-layout>
</template>


<script>
import ViewList from '../block/list';
import LayoutFrame from '../block/layout/frame-1';
import ViewGroup from '../group/start'
export default {
  components: {
    ViewGroup,
    LayoutFrame,
    ViewList,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      show:{},
    }
  },
  created(){
    this.show=this.data.userinfo;
  },
}
</script>

<style>

</style>


