<template>
  <div class="bg-white shadow-sm rounded p-3 fs-normal position-relative">
    <div class="fw-bold stretched-link" @click="$emit('geturl','id',data.id)"><span class="ybefore">{{$datename(data.eldate)}}</span><span class="ybefore">{{data.people}}人</span><span class="ybefore">愿拼车</span> <span class="badge rounded-pill bg-info fw-normal">顺风车</span></div>
    <div class="d-flex justify-content-between py-2">
      <div class="bd-highlight">
        <div class="ymerge">{{data.originname}}</div>
        <div class="ymerge">{{data.destinationname}}</div>
      </div>
      <div class="bd-highlight text-center">
        <span class="text-dark fs-4">{{data.price}}</span>元
        <div class="text-sm">含<span class="text-danger">{{data.price}}</span>元接单奖励</div>
      </div>
    </div>
    <div class="d-flex flex-row bd-highlight">
      <div class="p-1 bd-highlight position-relative stretched-link"  @click="$emit('geturl','user_id',data.user_id)">
        <span class="bi bi-star-fill text-warning"></span>{{data.user_nickname}}
      </div>
      <div class="p-1 bd-highlight"><span class="bi bi-star-fill text-warning"></span> 5.0</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data:{},
  },
  // data(){
  //   return {
  //     data:{
  //       name:'全部',
  //       number:'1',
  //       unit:'人',
  //     }
  //   }
  // }
}
</script>



